import { optionalSubjectCombineWithName } from "../../../../../Constant/Admission/admission_constant";
import style from "../../../../StoreManager/StoreManager.module.css";
const AdmissionSubjectCriteriaDetailNestedCardOptional = ({ subCri }) => {
  return (
    <div className={style.smc_card}>
      <div className={style.smc_card_contatiner}>
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {subCri?.optional_subject_name ?? subCri?.fixed_subject_name}
            </h6>
            <div
              className={style.smc_card_contatiner_inner}
              style={{
                marginLeft: "1.5rem",
              }}
            >
              {subCri?.optional_subject_options?.map((rt, index) => (
                <p key={order_index[index]}>
                  {`${order_index[index]})  `}
                  {rt?.subjectName}
                </p>
              ))}
              {subCri?.fixed_subject_options?.map((rt, index) => (
                <p key={order_index[index]}>
                  {`${order_index[index]})  `}
                  {rt?.subjectName}
                </p>
              ))}
              {subCri?.optional_subject_options_or?.length > 0 ? (
                <>
                  {subCri?.optional_subject_options_or?.map((rft, ind) => (
                    <p>
                      {`${
                        order_index[
                          subCri?.optional_subject_options?.length + ind
                        ]
                      })  `}
                      {optionalSubjectCombineWithName(rft?.options)}
                    </p>
                  ))}
                </>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdmissionSubjectCriteriaDetailNestedCardOptional;

const order_index = {
  0: "a",
  1: "b",
  2: "c",
  3: "d",
  4: "e",
  5: "f",
  6: "g",
  7: "h",
  8: "i",
  9: "j",
  10: "k",
  11: "l",
};
