import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useAdmissionOneSubGroupDetail } from "../../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import Loading from "../../../../../Loader/Loading";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useLocation } from "react-router-dom";
import AdmissionSubjectCriteriaDetailNestedCardMandatory from "./AdmissionSubjectCriteriaDetailNestedCardMandatory";
import AdmissionSubjectCriteriaDetailNestedCardOptional from "./AdmissionSubjectCriteriaDetailNestedCardOptional";
import AddAdmissionSubjectCriteriaDetailNested from "./AddAdmissionSubjectCriteriaDetailNested";

const AdmissionSubjectCriteriaDetailNested = ({ instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [add, setAdd] = useState(false);

  const { admissionOneSubGroupDetail, admissionOneSubGroupDetailLoading } =
    useAdmissionOneSubGroupDetail({
      data: {
        osid: getQuery.state?.nestedSubCri?._id,
      },
      skip: !getQuery.state?.nestedSubCri?._id,
    });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      {add ? (
        <AddAdmissionSubjectCriteriaDetailNested
          onClose={onToggle}
          instituteId={instituteId}
        />
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>{getQuery.state?.nestedSubCri?.group_name ?? ""}</h6>
              <div className={style.fm_search}>
                <CreateButton
                  label="add"
                  customStyle={{
                    paddingBlock: "0.4rem",
                    margin: "0",
                  }}
                  onAction={onToggle}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            {admissionOneSubGroupDetail?.compulsory_subject?.map((subCri) => (
              <AdmissionSubjectCriteriaDetailNestedCardMandatory
                subCri={subCri}
                key={subCri?._id}
              />
            ))}

            {admissionOneSubGroupDetail?.optional_subject?.map((subCri) => (
              <AdmissionSubjectCriteriaDetailNestedCardOptional
                subCri={subCri}
                key={subCri?._id}
              />
            ))}
            {admissionOneSubGroupDetail?.fixed_subject?.map((subCri) => (
              <AdmissionSubjectCriteriaDetailNestedCardOptional
                subCri={subCri}
                key={subCri?._id}
              />
            ))}
            {admissionOneSubGroupDetailLoading ? <Loading /> : null}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}

      {/* {add && (
        <AddAdmissionSubjectCriteriaDetail
          onClose={onToggle}
          sgid={getQuery.state?.subCri?._id}
          onRefetch={onRefetch}
        />
      )} */}
    </>
  );
};

export default AdmissionSubjectCriteriaDetailNested;
