import { useState } from "react";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useLocation } from "react-router-dom";
import AdmissionSubjectCriteriaDetailNestedCardMandatory from "./AdmissionSubjectCriteriaDetailNestedCardMandatory";
import AdmissionSubjectCriteriaDetailNestedCardOptional from "./AdmissionSubjectCriteriaDetailNestedCardOptional";
import InstituteSubject from "../../../../InstituteSubject/InstituteSubject";
import AddAdmissionSubjectCriteriaDetailNestedOptional from "./AddAdmissionSubjectCriteriaDetailNestedOptional";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import { useAdmissionSubGroupAddSubject } from "../../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import InstituteDepartment from "../../../../InstituteDepartment/InstituteDepartment";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";

const AddAdmissionSubjectCriteriaDetailNested = ({ onClose, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [step, setStep] = useState("");
  const [state, setState] = useState({
    cs: [],
    os: [],
    fs: [],
  });
  const [stateId, setStateId] = useState({
    cs: [],
  });
  const [extra, setExtra] = useState({
    department: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [admissionSubGroupAddSubject] = useAdmissionSubGroupAddSubject();

  const onSubjectCompulsory = (assing) => {
    if (stateId.cs?.includes(assing?._id)) {
      setState((prev) => ({
        ...prev,
        cs: prev.cs?.filter((dt) => dt?._id !== assing?._id),
      }));
      setStateId((prev) => ({
        ...prev,
        cs: prev.cs?.filter((dt) => dt !== assing?._id),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        cs: [...prev.cs, assing],
      }));
      setStateId((prev) => ({
        ...prev,
        cs: [...prev.cs, assing?._id],
      }));
    }
  };

  const onDone = () => {
    setStep("");
  };

  const onAddOptional = (data) => {
    setState((prev) => ({
      ...prev,
      os: [...prev.os, data],
    }));
    setStep("");
  };
  const onAddOptionalGp = (data) => {
    let gp_obj = {
      fixed_subject_rule: data?.optional_subject_rule ?? "",
      fixed_subject_rule_max: data?.optional_subject_rule_max ?? "",
      fixed_subject_name: data?.optional_subject_name ?? "",
      fixed_subject_options: data?.optional_subject_options ?? [],
      major_type: data?.major_type ?? "",
      major_select_max: data?.major_select_max ?? "",
      nested_select_name: data?.nested_select_name ?? "",
      nested_select_min: data?.nested_select_min ?? "",
    };
    setState((prev) => ({
      ...prev,
      fs: [...prev.fs, gp_obj],
    }));
    setStep("");
  };

  const onSelectDepartment = (dt) => {
    setExtra((prev) => ({
      ...prev,
      department: dt?._id,
    }));
    setStep("COMPULSORY");
  };
  const onSelectDepartmentOp = (dt) => {
    setExtra((prev) => ({
      ...prev,
      department: dt?._id,
    }));
    setStep("OPTIONAL");
  };
  const onSelectDepartmentGp = (dt) => {
    setExtra((prev) => ({
      ...prev,
      department: dt?._id,
    }));
    setStep("GP_OPTIONAL");
  };
  const onAddData = () => {
    if (
      getQuery.state?.nestedSubCri?._id &&
      (stateId?.cs?.length > 0 ||
        state?.os?.length > 0 ||
        state?.fs?.length > 0)
    ) {
      setDisabled((pre) => !pre);
      let cst = stateId.cs ?? [];
      let ost = [];
      let fst = [];

      for (let dt of state.os) {
        let st_id = [];
        for (let ft of dt?.optional_subject_options ?? []) {
          st_id.push(ft?._id);
        }
        let st_id_or = [];
        for (let ft of dt?.optional_subject_options_or ?? []) {
          let one_obj = {
            options: [],
          };
          for (let dft of ft?.options ?? []) {
            one_obj.options.push(dft?._id);
          }
          st_id_or.push(one_obj);
        }
        ost.push({
          optional_subject_rule: +dt?.optional_subject_rule,
          optional_subject_rule_max: +dt?.optional_subject_rule_max,
          optional_subject_name: dt?.optional_subject_name,
          optional_subject_options: st_id,
          optional_subject_options_or: st_id_or,

          major_type: dt?.major_type ?? "",
          major_select_max: dt?.major_select_max ? +dt?.major_select_max : 0,
          nested_select_name: dt?.nested_select_name ?? "",
          nested_select_min: dt?.nested_select_min ? +dt?.nested_select_min : 0,
        });
      }
      for (let dt of state.fs) {
        let st_id = [];
        for (let ft of dt?.fixed_subject_options ?? []) {
          st_id.push(ft?._id);
        }
        fst.push({
          fixed_subject_rule: +dt?.fixed_subject_rule,
          fixed_subject_rule_max: +dt?.fixed_subject_rule_max,
          fixed_subject_name: dt?.fixed_subject_name,
          fixed_subject_options: st_id,

          major_type: dt?.major_type ?? "",
          major_select_max: dt?.major_select_max ? +dt?.major_select_max : 0,
          nested_select_name: dt?.nested_select_name ?? "",
          nested_select_min: dt?.nested_select_min ? +dt?.nested_select_min : 0,
        });
      }

      admissionSubGroupAddSubject({
        ssid: getQuery.state?.nestedSubCri?._id,
        bodyParameter: {
          cs: cst,
          os: ost,
          fs: fst,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  // console.info("sdhfvsd", state);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {t("add_subject_in")}{" "}
              {getQuery.state?.nestedSubCri?.group_name ?? ""}
            </h6>
            <div className={style.fm_search}>
              <CreateButton
                label="save"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onAddData}
              />

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
              {/* <CreateButton
                label="cancel"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onClose}
              /> */}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6
              style={{
                fontWeight: "400",
                fontSize: "15px",
              }}
            >
              {t("add_compulsory_subject")}
            </h6>
            <div className={style.fm_search}>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setStep("DEPARTMENT")}
              />
            </div>
          </div>
          {state.cs?.map((subCri) => (
            <AdmissionSubjectCriteriaDetailNestedCardMandatory
              subCri={subCri}
              key={subCri?._id}
            />
          ))}

          <div
            className={style.fm_header_container}
            style={{
              marginTop: "1.5rem",
            }}
          >
            <h6
              style={{
                fontWeight: "400",
                fontSize: "15px",
              }}
            >
              {t("add_optional_subject")}
            </h6>
            <div className={style.fm_search}>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setStep("OP_DEPARTMENT")}
              />
            </div>
          </div>
          {state.os?.map((subCri) => (
            <AdmissionSubjectCriteriaDetailNestedCardOptional
              subCri={subCri}
              key={subCri?.optional_subject_name}
            />
          ))}

          <div
            className={style.fm_header_container}
            style={{
              marginTop: "1.5rem",
            }}
          >
            <h6
              style={{
                fontWeight: "400",
                fontSize: "15px",
              }}
            >
              {t("add_fixed_subject")}
            </h6>
            <div className={style.fm_search}>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setStep("GP_DEPARTMENT")}
              />
            </div>
          </div>
          {state.fs?.map((subCri) => (
            <AdmissionSubjectCriteriaDetailNestedCardOptional
              subCri={subCri}
              key={subCri?.fixed_subject_name}
            />
          ))}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {step === "DEPARTMENT" ? (
        <InstituteDepartment
          onSelectStaff={onSelectDepartment}
          title={t("select_department")}
          onClose={() => setStep("")}
          selectedId={extra?.department?._id}
          instituteId={instituteId}
        />
      ) : step === "COMPULSORY" ? (
        <InstituteSubject
          onDone={onDone}
          title={t("select_compulsory_subject")}
          onClose={() => setStep("")}
          selectList={stateId.cs}
          instituteId={extra?.department}
          isMultiSelect
          onMultiSelect={onSubjectCompulsory}
        />
      ) : step === "OP_DEPARTMENT" ? (
        <InstituteDepartment
          onSelectStaff={onSelectDepartmentOp}
          title={t("select_department")}
          onClose={() => setStep("")}
          selectedId={extra?.department?._id}
          instituteId={instituteId}
        />
      ) : step === "OPTIONAL" ? (
        <AddAdmissionSubjectCriteriaDetailNestedOptional
          onAddOptional={onAddOptional}
          onClose={() => setStep("")}
          instituteId={extra?.department}
        />
      ) : step === "GP_DEPARTMENT" ? (
        <InstituteDepartment
          onSelectStaff={onSelectDepartmentGp}
          title={t("select_department")}
          onClose={() => setStep("")}
          selectedId={extra?.department?._id}
          instituteId={instituteId}
        />
      ) : step === "GP_OPTIONAL" ? (
        <AddAdmissionSubjectCriteriaDetailNestedOptional
          onAddOptional={onAddOptionalGp}
          onClose={() => setStep("")}
          instituteId={extra?.department}
          isGroup
        />
      ) : null}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddAdmissionSubjectCriteriaDetailNested;
